<template>
  <div id="navbar">
    <el-menu
      class="el-menu-demo"
      mode="horizontal"
      background-color="#545c64"
      text-color="#fff"
      active-text-color="#ffd04b"
    >
      <el-menu-item
        ><router-link to="/home">
          <img src="../assets/toplearnerlogo.png" alt="" />
        </router-link>
      </el-menu-item>

      <el-menu-item
        :style="{ color: this.$route.name === 'home' ? 'white' : '#b1b1b1' }"
        class="home-tab"
      >
        <router-link to="/home">Home </router-link></el-menu-item
      >

      <el-menu-item
        :style="{ color: this.$route.name === 'about' ? 'white' : '#b1b1b1' }"
        ><router-link to="/about">About</router-link></el-menu-item
      >
      <el-menu-item class="logout-box" @click="dialogVisible = true">
        <el-tooltip
          class="item"
          effect="dark"
          content="Logout"
          placement="bottom"
        >
          <img src="./../assets/logout.png" alt="logout" class="logout" />
        </el-tooltip>
      </el-menu-item>
    </el-menu>
    <el-dialog center :visible.sync="dialogVisible" width="30%">
      <span>Are you sure you want to logout? </span>
      <span slot="footer" class="dialog-footer">
        <el-button @click="dialogVisible = false" size="mini">Cancel</el-button>
        <el-button type="primary" v-on:click="logout" size="mini">Ok</el-button>
      </span>
    </el-dialog>
    <div>
      <router-view />
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      isLoading: true,
      dialogVisible: false,
    };
  },

  methods: {
    logout() {
      this.dialogVisible = false;
      this.$router.push({ name: "login" });
    },
  },
};
</script>

<style lang="scss">
#navbar {
  .el-menu {
    z-index: 1000;
    width: 100%;
    display: flex;
    position: fixed;
  }
  .el-menu-item {
    font-size: larger;
  }
  .el-menu-item:hover {
    background: none !important;
  }
  a {
    text-decoration: none;
  }
  img {
    margin-left: 1rem;
    height: 2.5rem;
    width: 10rem;
  }
  .logout-box {
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: flex-end;
    .logout {
      padding: 0.1rem;
      float: right;
      height: 2.5rem;
      width: 2.5rem;
      border-radius: 50%;
    }
    .logout:hover {
      background: rgb(54, 54, 54);
    }
  }

  @media only screen and (max-width: "770px") {
    img {
      margin-left: 0rem !important;
      height: 2rem;
      width: 7rem;
    }
    .el-menu-item {
      padding: 0px 10px;
    }
    .logout {
      height: 2rem !important;
      width: 2rem !important;
    }
  }
}
</style>

<style>
.el-dialog__body {
  padding: 0px 1rem !important;
  float: left;
}
.el-dialog {
  height: 6rem !important;
  width: 20rem !important;
}
.el-dialog__footer {
  padding:28px 18px 6px 150px !important}
</style>
