<template>
  <div id="home-view">
    <div class="spinner" v-if="isPageContentLoading">
      <Spinner />
    </div>
    <div v-else>
      <h1>List of employees</h1>
      <div class="filter-box">
        <el-select
          v-model="userName"
          placeholder="Customer Name"
          v-on:change="selectTheUserName"
        >
          <el-option
            v-for="item in userNameArray"
            :key="item.name"
            :label="item.username"
            :value="item.username"
          >
          </el-option>
        </el-select>

        <el-select
          v-model="status"
          placeholder="Status"
          v-on:change="selectTheStatus"
        >
          <el-option
            v-for="item in statusArray"
            :key="item.value"
            :label="item.label"
            :value="item.value"
          >
          </el-option>
        </el-select>

        <el-button type="primary" v-on:click="clearAppliedFilters"
          >Clear</el-button
        >
        <el-tooltip content="Refresh" placement="top">
        <el-button
          class="el-icon-refresh-right"
          circle
          v-on:click="refresh"
        ></el-button>
      </el-tooltip>
      </div>
      <div class="table-view">
        <table id="customers" v-loading="isTableContentLoading">
          <tr>
            <th>ID</th>
            <th>USERID</th>
            <th>CUSTOMER NAME</th>
            <th class="width-of-title">CUSTOMER TITLE</th>
            <th class="width-of-status">TASK STATUS</th>
          </tr>
          <tr
            v-for="customer in completeData"
            :key="customer.id"
            :style="{
              'background-color':
                customer.completed === 'Not Completed' ? '' : '#50c050',
            }"
          >
            <td>{{ customer.id }}</td>
            <td>{{ customer.userId }}</td>
            <td>{{ customer.userName }}</td>
            <td class="width-of-title">{{ customer.title }}</td>
            <td class="width-of-status">{{ customer.completed }}</td>
          </tr>
        </table>
        <div v-if="errorWhileLoading" class="error-block">
          Error while loading the data..
        </div>
      </div>

      <Footer></Footer>
    </div>
  </div>
</template>

<script>
import axios from "axios";
import Footer from "./../components/Footer.vue";
import Spinner from "./../components/Spinner.vue";
import { alert } from "./../components/mixinAlert";

export default {
  name: "Home",
  created() {
    setTimeout(() => {
      this.isPageContentLoading = false;
    }, 1000);
    this.getTheEmployeesData();
  },
  components: {
    Footer,
    Spinner,
  },
  mixins: [alert],
  data() {
    return {
      data: [],
      completeData: [],
      originalData: [],
      filteredUserNameBasedData: [],
      filteredStatusBasedData: [],
      userNameArray: [],
      userName: "",
      status: "",
      statusArray: [
        {
          value: "Completed",
          label: "Completed",
        },
        { value: "Not Completed", label: "Not Completed" },
      ],
      errorWhileLoading: false,
      isTableContentLoading: true,
      isPageContentLoading: true,
    };
  },
  methods: {
    getTheEmployeesData() {
      this.clearAppliedFilters();
      this.completeData = [];
      this.isTableContentLoading = true;
      setTimeout(() => {
        this.getToDos();
        this.getUsers();
      }, 1000);
    },
    getToDos() {
      axios
        .get("https://jsonplaceholder.typicode.com/todos")
        .then((res) => {
          this.modifyTheData(res.data);
          this.isTableContentLoading = false;
          this.errorWhileLoading = false;
        })
        .catch((err) => {
          this.isTableContentLoading = false;
          this.errorWhileLoading = true;
          console.log(err);
        });
    },

    modifyTheData(data) {
      data.map((todos) => {
        if (todos.completed == true) {
          todos["completed"] = "Completed";
        } else {
          todos["completed"] = "Not Completed";
        }
      });
      this.data = data;
    },
    getUsers() {
      axios
        .get("https://jsonplaceholder.typicode.com/users")
        .then((res) => {
          this.userNameArray = res.data;
          this.userNameArray.map((users) => {
            this.data.map((todos) => {
              if (todos["userId"] === users.id) {
                todos["userName"] = users.username;
              }
            });
          });
          this.completeData = [...this.data];
          this.originalData = [...this.data];
        })
        .catch((err) => {
          console.log("error", err);
        });
    },

    selectTheUserName(userName) {
      if (
        this.filteredStatusBasedData.length > 0 &&
        this.filteredUserNameBasedData.length > 0
      ) {
        this.filteredUserNameBasedData = this.originalData.filter((ele) => {
          return userName === ele.userName;
        });
        this.filteredUserNameBasedData = this.filteredUserNameBasedData.filter(
          (ele) => {
            return this.status === ele.completed;
          }
        );
      } else if (
        this.filteredUserNameBasedData.length == 0 &&
        this.filteredStatusBasedData.length > 0
      ) {
        this.filteredUserNameBasedData = this.filteredStatusBasedData.filter(
          (ele) => {
            return userName === ele.userName;
          }
        );
      } else {
        this.filteredUserNameBasedData = this.originalData.filter((ele) => {
          return userName === ele.userName;
        });
      }
      this.completeData = [...this.filteredUserNameBasedData];
    },

    selectTheStatus(status) {
      if (
        this.filteredStatusBasedData.length > 0 &&
        this.filteredUserNameBasedData.length > 0
      ) {
        this.filteredStatusBasedData = this.originalData.filter((ele) => {
          return status === ele.completed;
        });
        this.filteredStatusBasedData = this.filteredStatusBasedData.filter(
          (ele) => {
            return this.userName === ele.userName;
          }
        );
      } else if (
        this.filteredUserNameBasedData.length > 0 &&
        this.filteredStatusBasedData.length == 0
      ) {
        this.filteredStatusBasedData = this.filteredUserNameBasedData.filter(
          (ele) => {
            return status === ele.completed;
          }
        );
      } else {
        this.filteredStatusBasedData = this.originalData.filter((ele) => {
          return status === ele.completed;
        });
      }
      this.completeData = [...this.filteredStatusBasedData];
    },
    clearAppliedFilters() {
      this.completeData = [...this.originalData];
      this.status = "";
      this.userName = "";
      this.filteredStatusBasedData = [];
      this.filteredUserNameBasedData = [];
      this.alertMessage = "Filtered cleared successfully";
    },
    refresh() {
      this.getTheEmployeesData();
      this.alertType = "success";
      this.alertMessage = "Refreshed Successfully";
      this.open();
    },
  },
};
</script>

