<template>
  <div id="login">
    <div v-if="isLoading" class="spinner flex-box">
      <Spinner />
    </div>
    <div v-else class="login-portal flex-box">
      <div class="help-box">
        <el-dropdown @command="copyTheText">
          <span class="el-dropdown-link">
            Help <i class="el-icon-arrow-down el-icon--right"></i>
          </span>
          <el-dropdown-menu slot="dropdown">
            <el-dropdown-item command="1"
              >Email:
              <span id="email">shawn@test-email.com</span>
            </el-dropdown-item>
            <el-dropdown-item command="2"
              >Password: <span id="password">shawnPass</span></el-dropdown-item
            >
          </el-dropdown-menu>
        </el-dropdown>
      </div>
      <h1>HR Portal</h1>
      <el-row class="login-form">
        <el-form
          :model="ruleForm"
          status-icon
          :rules="rules"
          ref="ruleForm"
          label-width="120px"
          class="demo-ruleForm flex-box"
        >
          <el-form-item label="" prop="emailID">
            <el-input
              type="email"
              placeholder="Please enter your EmailID"
              v-model="ruleForm.emailID"
            ></el-input>
          </el-form-item>
          <el-form-item label="" prop="password">
            <el-input
              placeholder="Please enter your Password"
              v-model="ruleForm.password"
              show-password
            ></el-input>
          </el-form-item>
          <el-form-item>
            <el-button type="primary" @click="submitForm('ruleForm')"
              >Submit</el-button
            >
            <el-button @click="resetForm('ruleForm')">Reset</el-button>
          </el-form-item>
        </el-form>
      </el-row>
    </div>
  </div>
</template>

<script>
import axios from "axios";
import Spinner from "./../components/Spinner.vue";
import { alert } from "./../components/mixinAlert";

export default {
  data() {
    var validatePass = (rule, value, callback) => {
      if (value === "") {
        callback(new Error("Please provide the EmailID"));
      } else if (value.match("^(.+)@(.+)$") === null) {
        callback(new Error("EmailID is not valid"));
      }
      callback();
    };
    var validatePass2 = (rule, value, callback) => {
      if (value === "") {
        callback(new Error("Please provide the password"));
      }
      callback();
    };
    return {
      isLoading: true,
      validEmail: "",
      validPassword: "",
      ruleForm: {
        emailID: "",
        password: "",
      },
      rules: {
        isLoading: false,
        emailID: [{ validator: validatePass, trigger: "blur" }],
        password: [{ validator: validatePass2, trigger: "blur" }],
      },
      alertMessage: "",
      alertType: "",
    };
  },
  created() {
    setTimeout(() => {
      this.isLoading = false;
    }, 1000);
  },
  components: {
    Spinner,
  },
  mixins: [alert],
  methods: {
    copyTheText(data) {
      if (data == "1") {
        var copyText = document.getElementById("email");
        this.$message({
          message: "Email Copied",
          type: "success",
        });
      } else {
        var copyText = document.getElementById("password");
        this.$message({
          message: "Password Copied",
          type: "success",
        });
      }
      navigator.clipboard.writeText(copyText.textContent);
    },
    submitForm(formName) {
      this.$refs[formName].validate((valid) => {
        if (valid) {
          console.log("valid");
          this.isCredentialsValid();
        } else {
          console.log("error submit!!");
          return false;
        }
      });
    },
    resetForm(formName) {
      this.$refs[formName].resetFields();
    },
    isCredentialsValid() {
      let emailID = this.ruleForm.emailID.trim();
      let password = this.ruleForm.password.trim();
      axios
        .get(
          `https://hr.toplearnr.com/recruitment/Api/endpoint/?email=${emailID}&password=${password}`
        )
        .then((res) => {
          if (res.data.msg === "Success") {
            this.alertType = "success";
            this.alertMessage = "LoggedIn Successfully";
            this.$router.push({ name: "home" });
          } else {
            this.alertType = "warning";
            this.alertMessage = "Invalid Credentials";
          }
          this.open();
        })
        .catch((err) => {
          console.log(err);
          this.alertType = "error";
          this.alertMessage = "Something went wrong";
          this.open();
        });
    },
  },
};
</script>
